:root {
  
  box-sizing: border-box;
  --visited: #f08615;
  --transition: #eaa133;
  --shortest: rgb(255, 255, 255);
  display: inline-block;
 
}

.grid-container {
  padding: 1vw;
  margin-right: 1vw;
  border-radius: 1vw;
  box-shadow: 0.1px 0.1px 5px black;
  width: 100%;
}
.grid {
  border-collapse: collapse;
  width: 100%;
  height: 100%;
}
.grid td {
  border: 0.1px solid rgba(7, 7, 7, 0.13);
  width: 1fr;
  height: 1fr;
  padding: 0;
}

.node {
  padding: 0;
}
.visited-anim {
  animation: onvisit 0.5s;
  background-color: var(--visited);
}
.visited {
  background-color: var(--visited);
  animation-name: visitedAnimation;
    animation-duration: 1.5s;
    animation-timing-function: ease-out;
    animation-delay: 0;
    animation-direction: alternate;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    animation-play-state: running;
}
@keyframes visitedAnimation{
  0% {
    transform: scale(0.3);
    background-color: rgba(17, 104, 217, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(63, 251, 135, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(63, 151, 251, 0.75);
  }
}
.shortest-path-anim {
  background-color: var(--visited);
  background-image: linear-gradient(var(--shortest), var(--shortest));
  background-position: 50% 50%;
}
.wall {
  background-image: linear-gradient(black, black) !important;
  border: 0.1px solid black;
}
.shortest-path {
  background-image: linear-gradient(var(--shortest), var(--shortest));
  background-color: var(--visited);
}
.end-node {
  background-image: linear-gradient(red, red) !important;
  border: 0.1px solid red !important;
}
.start-node {
   
  background-image: linear-gradient(green, green) !important;
  border: 0.1px solid green !important;
}
@keyframes onvisit {
  0% {
    transform: scale(0.6);
    background-color:  rgb(234, 211, 7);
  }
  75% {
    transform: scale(0.6);
    background-color: rgb(55, 85, 255);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(234, 211, 7);
  }

  100% {
    transform: scale(1);
    background-color: rgb(55, 85, 255);
  }
}
.app {
  display: flex;
}
.content {
  display: flex;
  width: 100%;
  --margin-top: 7vh;
  padding: 1vw 1vw 1vw 1vw;
}
.data {
  width: 40%;
  height: auto;
}

.dataContent {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.shortestPathCard {
  background-image: linear-gradient(
    to bottom right,
    var(--visited),
    var(--transition)
  );
}

@media only screen and (max-width: 1400px) {
  .content {
    flex-direction: column;
  }
  .data {
    width: 100%;
    height: 200px;
    margin-top: 2vh;
  }
  .dataContent {
    flex-direction: row;
  }
}

*::-webkit-scrollbar-track {
  background-color: #192038;
}

*::-webkit-scrollbar {
  width: 0.3125rem;
  background-color: #101426;
}
*::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #101426;
}
div::-webkit-scrollbar-track {
  background-color: #192038;
}

div::-webkit-scrollbar {
  width: 0.3125rem;
  background-color: #101426;
}
